import React from "react";
import { Link, useLocation } from "react-router-dom";
// import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
// import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';

function Header() {

  let location = useLocation();
  let user = window.sessionStorage.getItem("user");

  return (
    <div>
      <Navbar bg="light" expand="lg">
        <Container fluid>
          <Navbar.Brand href="/recruiter">Elihr</Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" className="shadow-none" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: '250px' }}
              navbarScroll
            >
              {location.pathname === "/"||"recruiter" ? "" : <Nav.Link href="/recruiter">Home</Nav.Link>}
              <Nav.Link href="/recruiter/about">About</Nav.Link>
              <NavDropdown title="Service" id="navbarScrollingDropdown">
                <NavDropdown.Item href="#">Talent Seeker</NavDropdown.Item>
                <NavDropdown.Item href="#">Job Seeker</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="/recruiter/career">Career</NavDropdown.Item>
                <NavDropdown.Item href="/recruiter/blogs">Blog</NavDropdown.Item>
              </NavDropdown>
            </Nav>

            {!user ? <Link to={"/recruiter/auth/signin"} className="text-muted p-2">Sign in</Link> : ""}
            {!user ? <Link to={"/recruiter/auth/signup"} className="text-muted p-2">Sign up</Link> : ""}
            {user ? (<Link to={"/recruiter/users/dashboard"} className="text-muted p-2">Account</Link>) : ""}
            {user ? <Link to={"/recruiter/auth/signin"} className="text-muted p-2">Sign out</Link> : ""}

            {/* <Form className="d-flex">
              <Form.Control
                type="search"
                placeholder="Search"
                className="me-2"
                aria-label="Search"
              />
              <Button variant="outline-success">Search</Button>
            </Form> */}
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  )
}
export default Header;